html {
  height: 100%;
}

body {
  font-family: sans-serif;
  padding: 0 10px;
  height: 100%;
  background: black;
  margin: 0;
}

h1 {
  color: white;
  margin: 0;
  padding: 15px;
}

#container {
  text-align: center;
  margin: 0;
}

#qr-canvas {
  margin: auto;
  width: calc(100% - 20px);
  max-width: 400px;
}

#btn-scan-qr {
  cursor: pointer;
}

#btn-scan-qr img {
  height: 10em;
  padding: 15px;
  margin: 15px;
  background: white;
}

#qr-result {
  font-size: 1.2em;
  margin: 20px auto;
  padding: 20px;
  width: auto;
  max-width: 300px;
  background-color: white;
}

#error {
  font-size: 1.2em;
  margin: 20px auto;
  padding: 20px;
  width: auto;
  max-width: 300px;
  background-color: rgb(255, 120, 120);
}


#inputdiv {
  font-size: 1.2em;
  margin: 20px auto;
  padding: 10px 20px;
  max-width: 200px;
  background-color: white;
  cursor: pointer;
}

#backButton {
  font-size: 2em;
  margin: 30px 0px 20px 0px;
  padding: 20px;
  width: 243px;
  background-color: white;
  cursor: pointer;
}

input[type=number] {
  text-align: center;
  min-width: 100px;
  width: 70%;
  padding: 12px 20px;
  margin: 8px 0;
  -moz-appearance: textfield;
  margin: 0;
  box-sizing: border-box;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}